		let age = search.get("age");
		let color = search.get("color");
		let size = search.get("size");
		let gender = search.get("gender");
		let price = search.get("price");

		switch (filterType) {
			case "Age":
				filter = { name: "Age", value: [] };
				age = "null";
				break;

			case "Color":
				filter = { name: "Color", value: [] };
				color = "null";
				break;

			case "Size":
				filter = { name: "Size", value: [] };
				size = "null";
				break;

			case "Gender":
				filter = { name: "Gender", value: [] };
				gender = "null";
				break;

			default:
				return "null";
		}

