const mapStateToProps = (state, ownProps) => {
	return {
		productList: state.CategoryParentPage.category.productList,
		categories: state.CategoryParentPage.category.categories,
		filters: state.CategoryParentPage.category.filters,
		cat_tags: state.CategoryParentPage.category.cat_tags,
		logoData: state.CategoryParentPage.logo.logoData,
		slidesToShow: state.CategoryParentPage.logo.slidesToShow,
		category_seo: state.CategoryParentPage.category.category_seo,
		// selectedFilter : state.CategoryParentPage.filter.selectedFilter
	};
};

const mapDispatchToProps = (dispatch, ownProps) => {
	return bindActionCreators(
		{
			getCategoryProducts: Actions.getCategoryProducts,
			getLogos: Actions.getLogos,
			getCategorySEO: Actions.getCategorySEO,
			handleSortProduct: Actions.handleSortProduct,
			// filterCollection: Actions.filterCollection
		},
		dispatch
	);
};

export default withReducer(
	"CategoryParentPage",
	reducer
)(connect(mapStateToProps, mapDispatchToProps)(CategoryParentPage));
